/* Icon 4 - By Jesse Couch Codepen: https://codepen.io/designcouch*/
/* Forked Codepen: https://codepen.io/designcouch/pen/Atyop*/
/* Modified by Edgar Carrillo */

.nav-icon4 {
  z-index: 100;
  width: 60px;
  height: 45px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  cursor: pointer;
  margin-top: 10px;
}

.nav-icon4 span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: #c4c4c4;
  /* border-radius: 9px; */
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.nav-icon4 span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-icon4 span:nth-child(2) {
  top: 15px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-icon4 span:nth-child(3) {
  top: 30px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-icon4.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -3px;
  left: 8px;
}

.nav-icon4.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.nav-icon4.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 39px;
  left: 8px;
}

.mobile-nav {
  position: fixed;
  width: 100%;
  height: 80px;
  border-bottom: 2px solid #c4c4c4;
}

.mobile-nav-items-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-right: 30px;
  padding-left: 30px;
  height: 100%;
}

.nav-logo {
  font-family: 'Roboto-Medium';
  color: #0083ff;
  font-size: 1.4rem;
}

.ham-btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 60px;
}

.dd-container {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: absolute;
  margin-top: 340px;
  overflow: hidden;
  height: 260px;
  width: 150px;
}

.dd-menu {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  align-items: center;
  width: 100px;
  height: 250px;
  border-radius: 0 0 50px 50px;
  border: 2px solid #c4c4c4;
  transition: margin-top 1s;
  margin-top: -300px;
}

/* Makes the dd-menu disappear */
.dd-menu.open-dd {
  margin-top: 0;
}

.dd-menu > li {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-color: #0083ff;
}

.form-container {
  max-width: 1000px;
  height: 200px;
  margin: 10px auto 0px;
  margin-left: 20px;
  margin-right: 20px;
}

.intro-text-container {
  padding-top: 80px;
}

.intro-text-container > h1 {
  font-family: 'Roboto-Bold';
  font-size: 2.5rem;
  line-height: 1.3;
}

.intro-text-complementary-text {
  margin-top: 15px;
  font-family: 'Roboto-Regular';
  font-size: 1.5rem;
  color: #8d8d8d;
  line-height: 1.3;
}
